<template>
  <div class="Pagination clearfix" v-if="total && pages>0">
    <div class="pre" @click="handleClick(ownPage-1)" :class="{dis: ownPage-1<1}">
      <div class="icon-box">
        上一页
      </div>
    </div>
    <div class="num" :class="{ now: ownPage===1 }" @click="handleClick(1)">
      <span class="num-txt">1</span>
    </div>
    <div class="num es" v-if="ellipsis[0]">
      <span class="ellipsis">...</span>
    </div>
    <div class="num" v-for="(num,index) in showNums" :class="{ now: ownPage===num }" @click="handleClick(num)">
      <span class="num-txt">{{num}}</span>
    </div>
    <div class="num es" v-if="ellipsis[1]">
      <span class="ellipsis">...</span>
    </div>
    <div class="num" v-if="pages>1" :class="{ now: ownPage===pages }" @click="handleClick(pages)">
      <span class="num-txt">{{pages}}</span>
    </div>
    <div class="next" @click="handleClick(ownPage+1)" :class="{dis: ownPage+1>pages}">
      <div class="icon-box">
        下一页
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    page: {
      type: Number,
      // required: true,
      default () {
        return 1
      }
    },
    total: {
      type: Number,
      // required: true,
      default: 110
    },
    limit: {
      type: Number,
      default: 10
    }
  },
  data () {
    return {
      btn_limit: 5,
      threshold: 10,
      ownPage: 1
    }
  },
  computed: {
    pages () {
      return Math.ceil(this.total / this.limit)
    },
    showNums () {
      let btns = []; let resbtn = []
      // 小于10页显示全部页码
      if (this.pages <= this.threshold) {
        btns = [2, this.pages - 1]
      } else {
        let aboutLimit = this.btn_limit / 2
        // 前几页
        if (this.ownPage <= aboutLimit + 1.5) {
          btns = [2, this.btn_limit]
        }
        // 后几页
        else if (this.ownPage >= this.pages - aboutLimit - 1) {
          btns = [this.pages - this.btn_limit + 1, this.pages - 1]
        } else {
          aboutLimit = Math.ceil(aboutLimit)
          btns = [this.ownPage - aboutLimit + 1, this.ownPage - aboutLimit + this.btn_limit]
        }
      }
      for (let i = btns[0]; i <= btns[1]; i++) {
        resbtn.push(i)
      }
      return resbtn
    },
    ellipsis () {
      return [this.showNums[0] && this.showNums[0] !== 2, this.showNums[this.showNums.length - 1] && this.showNums[this.showNums.length - 1] !== this.pages - 1]
    }
  },
  watch: {
    page (val) {
      this.ownPage = val
    }
  },
  mounted () {
    this.ownPage = this.page
  },
  methods: {
    handleClick (pageNow) {
      if (pageNow < 1 || pageNow > this.pages || this.ownPage == pageNow) return
      let oldPage = this.ownPage
      this.ownPage = pageNow
      this.$emit('handleChange', pageNow, oldPage)
      console.log(pageNow, oldPage)
    }
  }
}
</script>

<style lang="scss" scoped>
  .Pagination {
		display: inline-block;
		white-space: nowrap;
		margin-left: -3px;
    padding-left: 1px;
    .num, .pre, .next {
			height: 24px;
			min-width: 24px;
			border-radius: 1px;
			border:1px solid rgba(230,233,233,1);
			box-sizing: border-box;
      display: inline-block;
			font-size: 12px;
			line-height: 12px;
			color: rgba(28,29,29,1);
			vertical-align: middle;
			padding: 5px;
			text-align: center;
			margin-left: 3px;
			cursor: pointer;
			background: #FFFFFF;
			&.dis{
				background: #EDEDED;
				color: #FFFFFF;
			}
    }
    .num {
			&.es{
				border:1px solid transparent;
				background: transparent;
				cursor: default;
			}
			&.now {
				border: 1px solid #1CD7D1;
				.num-txt {
				  color: #1CD7D1;
				}
			}
      .num-txt {
        color: #1C1D1D;
      }
      .ellipsis {
        color: #1C1D1D;
      }
    }
  }
</style>
