<template>
  <div class="product-wrapper">
    <div class="list clearfix" >
      <div class="part-lt fl" :style="comLeftWidth">
        <div class='pName'>{{productInfo.name}}</div>
        <div class="pMsg">
          <span class="condition-item" v-for="(item,index) in productInfo.insure_condition" :key="index">{{item.name}}：{{item.value}}</span>
        </div>
        <div class="marks clearfix">
          <div class='mark fl' >
            <span class="fl" v-if="productInfo.feature1"><i></i>{{productInfo.feature1}}</span>
            <span class="fl" v-if="productInfo.feature2"><i></i>{{productInfo.feature2}}</span>
            <span class="fl" v-if="productInfo.feature3"><i></i>{{productInfo.feature3}}</span>
          </div>
        </div>
        <div class="characteristics">
          <div class="character clearfix" v-for="(item,index) in parameters.entries" :key="index">
            <span class="fl">{{item.name}}</span> 
            <span class="fr">{{item.limit}}</span>
          </div>
        </div>
        <div class="logo"><img :src="baseSrc+productInfo.brandLogo" alt="公司logo"></div>
      </div>
      <div class="part-rt" :style="comRightWidth">
        <div class="ctx">
          <div class="price"><span class="p">{{productInfo.price}}</span><span class="b">元起</span></div>
          <div class="toDetail" @click="goDetail(productInfo.id)">查看详情</div>
          <slot></slot>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  props: {
    leftWidth: {
      type: Number
    },
    rightWidth: {
      type: Number,
      default: 260
    },
    productInfo: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      baseSrc: '',
      parameters: []
    }
  },
  mounted () {
    this.baseSrc = `${process.env.VUE_APP_RESOURCE_URL}`
    if (this.productInfo.parameter_values === null) return
    this.parameters = this.productInfo.parameter_values[0]
  },
  computed: {
    comLeftWidth () {
      return {
        width: this.leftWidth ? this.leftWidth + 'px' : '800px'
      }
    },
    comRightWidth () {
      return {
        width: this.rightWidth ? this.rightWidth + 'px' : '800px'
      }
    }
  },
  methods: {
    goDetail (id) {
      this.$router.push({
        path: `/37duc/product/detail/${id}`,
        query: {
          productId: id
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped="scoped">
  @import '@/assets/styles/mixin.scss';
  .product-wrapper {

    .list {
      margin-bottom: 12px ;
      height: 250px;
      background: #FFFFFF;
      box-shadow: 0 2px 4px 0 rgba(219, 219, 219, .35);
      .part-lt {
        padding: 12px 20px;
        position: relative;
        /*width: 800px;*/
        .pName {
          font-size: 20px;
          color: $color-text;
          line-height: 36px;
          letter-spacing: 1px;
          font-weight: 600;
        }
        .pMsg {
          font-size: 14px;
          color: #B6BBBB;
          line-height: 30px;
          white-space: nowrap;
          .condition-item {
            font-size: 14px;
            color: #B6BBBB;
            &:not(:last-child):after {
              content: '|';
            }
          }
        }
        .marks {
          margin: 12px -40px 12px 0;
          .mark {
            margin-right: 40px;
            i {
                display: inline-block;
                vertical-align: top;
                margin-top: 1px;
                background: url("../assets/images/icon_ts.png") no-repeat;
                background-size: 16px 16px;
                width: 16px;
                height: 16px;
                margin-right: 8px;
              }
            img.fl {
              width: 16px;
              height: 16px;
            }
            span {
              padding-left: 10px;
              line-height: 16px;
              color: $color-text;
              font-size: 14px;
              margin-right:40px;
              &:first-child{
                padding-left: 0;
              }
            }

          }
        }
        .characteristics {
          .character {
            line-height: 30px;
            font-size: 14px;
            color: $color-text;
          }
        }
        .logo {
          position: absolute;
          right: 20px;
          top: 20px;
          width: 80px;
          height: 40px;
          overflow: hidden;
          /*background: rgba(0, 166, 0, .6);*/
          img {
            float: right;
            width:80px;
            height: 40px;
          }
        }
      }
      .part-rt {
        position: relative;
        overflow: hidden;
        float: right;
        background-color: rgba(241, 244, 244, .5);
        height: 100%;
        width:260px;
        .ctx {
          position: absolute;
          top: 50%;
          left: 0;
          right: 0;
          margin: -68px 0;
          .price {
            margin: -3px 0 30px;
            text-align: center;
            white-space: nowrap;
            .p {
              line-height: 33px;
              height: 33px;
              display: inline-block;
              font-size: 30px;
              color: $color-active-text;
            }
            .b {
              line-height: 20px;
              font-size: 12px;
              height: 20px;
              display: inline-block;
              color: #D5D8D8;
            }
            .p, .b {
              vertical-align: bottom;
            }
          }
          .toDetail {
            width: 130px;
            height: 40px;
            line-height: 40px;
            background-color: $color-active-text;
            color: #FFFFFF;
            font-size: 16px;
            text-align: center;
            border-radius: 20px;
            margin: 0 auto;
            cursor: pointer;
            letter-spacing: 1px;
          }
          .cancelCollect{
            cursor: pointer;
            margin-top: 30px;
            font-size: 16px;
            color: #A5A8A8;
            text-align: center;
          }
        }
      }
    }
  }
</style>
