<template>
	<div class="pdct-l-wp layout" v-if="tabs.length">
		<TheCompiler :tabs='tabs' @changeTab='handleTabChange'></TheCompiler>
		<!--<TheContentList :message='message' @toDetail='handleToDetail' v-if='showList'></TheContentList>-->
		<ul v-if="!noItem">
			<li v-for="(item,index) in filterList" :key="index">
				<product :leftWidth="800" :productInfo="item">
				</product>
			</li>
		</ul>
		<div class="noFoundProduct" v-else>
			<div class="ctx">
				<img src="@/assets/images/product-icon.png">
				该分类下暂无产品
			</div>
		</div>
		<div class="ThePaginationBar">
			<ThePaginationBar :page="filterPage" :total="filterTotal" @handleChange='handlePageChange'></ThePaginationBar>
		</div>
	</div>
</template>

<script>
import TheCompiler from '../components/product/list/TheCompiler.vue'
import TheContentList from '../components/product/list/TheContentList'
import ThePaginationBar from '@/components/ThePaginationBar.vue'
import product from '@/components/BaseProduct'

export default {
  data () {
    return {
      tabs: [],
      showList: true,
      page: {},
      total: {},
      lists: {},
      noItem: false
    }
  },
  methods: {
    handleToDetail (id) {
      this.$router.push({
        path: '/37duc/product/detail/' + id,
        query: {
          productId: id
        }
      })
    },
    handleTabChange (item) {
      this.tabs.find(tab => {
        tab.active = false
      })
      item.active = true
      this.$router.push(`/37duc/product/list?productCategoryId=${this.productCategoryId}&page=${this.filterPage}`)
    },
    handlePageChange (val) {
      this.$set(this.page, `ID${this.productCategoryId}`, val)
      this.$router.replace(`/37duc/product/list?productCategoryId=${this.productCategoryId}&page=${this.filterPage}`)
    },
    getMessage () {
      this.noItem = false
      return new Promise((resolve, reject) => {
        this.$api.product.getProductListWidthType({
          productCategoryId: this.productCategoryId,
          page: this.filterPage,
          limit: 8
        }).then(res => {
          resolve(res)
        })
      })
    },
    renderList () {
      this.getMessage().then(res => {
        console.log('res', JSON.parse(JSON.stringify(res)))
        this.$set(this.total, `ID${this.productCategoryId}`, res.count)
        this.$set(this.lists, `ID${this.productCategoryId}`, res.data)
        this.noItem = !res.data.length
      })
    }
  },
  computed: {
    productCategoryId () {
      let t = this.tabs.find(tab => tab.active)
      console.log(t)
      return t ? t.id : false
    },
    filterPage () {
      return this.page[`ID${this.productCategoryId}`] || 1
    },
    filterTotal () {
      return this.total[`ID${this.productCategoryId}`] || 0
    },
    filterList () {
      return this.lists[`ID${this.productCategoryId}`] || []
    }
  },
  created () {
    this.$api.product.getProductType()
      .then((data) => {
        data.forEach((v, k) => this.tabs.push({
          tabTxt: v.name,
          active: k == 0,
          id: v.id
        }))
        if (!this.tabs.length) {
          this.$router.replace('/37duc/404')
        } else {
          if (!Object.keys(this.$route.query).length) {
            this.$router.replace(`/37duc/product/list?productCategoryId=${this.productCategoryId}&page=${this.filterPage}`)
          } else {
            let ID = this.$route.query.productCategoryId
            this.tabs.forEach(tab => tab.active = tab.id == ID)
            this.$set(this.page, `ID${this.productCategoryId}`, parseInt(this.$route.query.page))
            this.renderList()
          }
        }
      })
  },
  watch: {
    '$route': {
      handler (val) {
        let ID = val.query.productCategoryId
        this.tabs.forEach(tab => tab.active = tab.id == ID)
        this.$set(this.page, `ID${this.productCategoryId}`, parseInt(val.query.page))
        this.renderList()
      }
    }
  },
  components: {
    TheCompiler,
    TheContentList,
    ThePaginationBar,
    product
  }
}
</script>

<style lang="scss" scoped="scoped">
	.pdct-l-wp {
		wdith: 1100px;
		margin: 12px auto;

		.noFoundProduct {
			min-height: 626px;
			background: #FFFFFF;
			box-shadow: 0 2px 4px 0 rgba(219, 219, 219, 0.35);
			position: relative;

			.ctx {
				position: absolute;
				left: 0;
				top: 50%;
				margin-top: -60px;
				width: 100%;
				text-align: center;
				font-size: 20px;
				color: #D5D8D8;
				line-height: 20px;

				img {
					display: block;
					margin: 0 auto;
					width: 64px;
					height: 72px;
					margin-bottom: 28px;
				}
			}
		}

		.ThePaginationBar {
			margin: 15px 0;
			text-align: right;
		}
	}
</style>
